import { IonApp, IonRouterOutlet, IonSplitPane } from '@ionic/react';
import { IonReactRouter } from '@ionic/react-router';
import { Redirect, Route } from 'react-router-dom';
import Menu from './components/Menu';
import HomeRoute from './routers/HomeRoute';
import PrivateRoute from './routers/PrivateRoute';
import PrivateLayout from './layouts/PrivateLayout';
import SettingsPage from './pages/private/SettingsPage';
import CampaignsPage from './pages/private/Campaigns';
import DashboardPage from './pages/private/DashboardPage';
import IntroPage from './pages/public/IntroPage';

/* Core CSS required for Ionic components to work properly */
import '@ionic/react/css/core.css';

/* Basic CSS for apps built with Ionic */
import '@ionic/react/css/normalize.css';
import '@ionic/react/css/structure.css';
import '@ionic/react/css/typography.css';

/* Optional CSS utils that can be commented out */
import '@ionic/react/css/padding.css';
import '@ionic/react/css/float-elements.css';
import '@ionic/react/css/text-alignment.css';
import '@ionic/react/css/text-transformation.css';
import '@ionic/react/css/flex-utils.css';
import '@ionic/react/css/display.css';

/* Theme variables */
import './theme/variables.css';

const App: React.FC = () => {
  return (
    <IonApp>
      <IonReactRouter basename={process.env.PUBLIC_URL ?? '/'}>
        <IonRouterOutlet>
          <HomeRoute path="/" exact={true} />
          <Route path="/intro" exact={true}>
            <IntroPage />
          </Route>
          <PrivateLayout>
            <PrivateRoute path="dashboard" exact={true}>
              <DashboardPage></DashboardPage>
            </PrivateRoute>
            <PrivateRoute path="/settings" exact={true}>
              <SettingsPage></SettingsPage>
            </PrivateRoute>
            <PrivateRoute path="/campaigns" exact={true}>
              <CampaignsPage></CampaignsPage>
            </PrivateRoute>
          </PrivateLayout>
        </IonRouterOutlet>
      </IonReactRouter>
    </IonApp>
  );
};

export default App;
